<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquiose: #0babc5;

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

//paddings and margins
$pd-desktop: 0 pxToEm(390);
$mg-desktop: 0 pxToEm(40);

//circle
$circle: 50%;

.especialidades-wrapper {
  background-color: $white;
  height: pxToEm(990);
  padding: $pd-desktop;

  .especialidades {
    height: 100%;
    margin: $mg-desktop;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    .top {
      display: flex;
      flex-direction: column;
      align-items: center;

      .title {
        color: #828282;
        font-size: pxToEm(30);
        margin-top: pxToEm(30);
      }

      .line {
        width: pxToEm(80);
        height: pxToEm(1);
        background-color: $turquiose;
        margin-top: pxToEm(20);
      }

      .copy {
        color: #8e8e8e;
        font-size: pxToEm(14);
        margin-top: pxToEm(30);
        max-width: pxToEm(800);
        width: 100%;
        text-align: center;
      }
    }

    .topics {
      margin-top: pxToEm(60);
      display: flex;
      justify-content: space-between;
      // width: 65%;

      .topic {
        width: pxToEm(260);
        height: pxToEm(340);
        background-color: #f6f6f6;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding: pxToEm(40);
        transition: .5s;
        cursor: pointer;
        margin: 0 pxToEm(10);

        &:hover {
            background-color: #95989A;
            transform: scale(1.1);

            > .title, .txt {
                color: $white;
            }
        }

        > .title {
          color: #707070;
          font-size: pxToEm(20);
        }

        .txt {
          font-family: "AHG-Regular";
          color: #7b7b7b;
          font-size: pxToEm(16);
          margin-top: pxToEm(10);
        }

        .cta {
            cursor: pointer;
            align-self: flex-end;

            .txt {
                text-transform: uppercase;
            }
        }
      }
    }
  }
}
</style>



